import React from 'react';
import { BlipChat } from "blip-chat-widget";

const Blip = (props) =>{
  
  let blipClient = new BlipChat()

  function start(){
    blipClient.build()
  }

  function toogle(){
    blipClient.toogleChat();
  }

  function appKey(key){
    blipClient.withAppKey(key)
  }
  
  function withStyle(){
    // Header - background -  balao chat
    blipClient.withCustomStyle("#blip-chat-header { background-color: #0F0 !important; } #messages-list { background-color: #F00; } .bubble .right { background-color: #FF0; !important}")
  }

  function withButton(){
    blipClient.withButton({
      icon:
        'https://cdn.iconscout.com/icon/free/png-256/fiat-5-569191.png'
    })
  }

//   function stop(){
//     blipClient.destroy();
//   }
  // FAZ A CONSTRUÇÃO BÁSICA DO CHAT
  function build(){
    
    appKey("cm9iaW46ZjhjZDMxNmEtZTg5Ni00ZDE2LWFmODMtMjg4N2NiNGMxYjQy")
    withStyle()
    withButton()
    start()
    toogle()
  }
  //FAZ A RECONSTRUÇÃO DO CHAT
//   function rebuild(){
  
//       withStyle()
//       start()

    
//   }

  return(
    // cm9iaW46ZjhjZDMxNmEtZTg5Ni00ZDE2LWFmODMtMjg4N2NiNGMxYjQy
    <> 
      {build()}
    </>
  )
}

export default Blip